<template>
	<div class="container">
        <div class="blog-article" v-for='article in articles' :key='article.index'>
           <div v-if='article.id === proId'>
                <FormPost :proId='proId' :article='article' />
            </div>
        </div>
        <!-- /.menu-item -->
	</div>
</template>
<script>
const FormPost = () => import('./FormPost');

export default {
    name: 'editArticle',
    data() {
        return {
            proId: JSON.parse(localStorage.getItem('articleId')) || this.$route.params.id,
            articles: [],
        }
    },
    firestore () {
        return {
            articles: this.$_db.collection('blog').orderBy('createdAt')
        }
    },
    components: {
        FormPost
    }
}
</script>
